"use client";

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow} from '@/components/ui/table';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { toast } from 'sonner';
import Link from 'next/link';

type Ink = { id: number; name: string; shade: string; };

export default function DeletedInksPage() {
  const [deletedInks, setDeletedInks] = useState<Ink[]>([]);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [selectedInk, setSelectedInk] = useState<Ink | null>(null);
  const [confirmText, setConfirmText] = useState("");

  async function fetchDeletedInks() {
    const response = await fetch('/api/inks?deleted=true'); 
    if (response.ok) setDeletedInks(await response.json());
  }

  useEffect(() => { fetchDeletedInks(); }, []);

  const handleRestore = async (ink: Ink) => {
    const response = await fetch(`/api/inks/${ink.id}`, {
        method: 'PATCH', // Use PATCH for restoring
    });
    if (response.ok) {
        toast.success(`Ink "${ink.name}" restored successfully!`);
        fetchDeletedInks();
    } else {
        toast.error("Failed to restore ink.");
    }
  };

  const openDeleteDialog = (ink: Ink) => {
    setSelectedInk(ink);
    setConfirmText("");
    setDialogOpen(true);
  };

  const handlePermanentDelete = async () => {
    if (!selectedInk || confirmText !== "Delete") {
      toast.error("You must type 'Delete' to confirm.");
      return;
    }

    const response = await fetch(`/api/inks/${selectedInk.id}?permanent=true`, { method: 'DELETE' });
    if (response.ok) {
        toast.success(`Ink "${selectedInk.name}" permanently deleted!`);
        fetchDeletedInks();
        setDialogOpen(false);
        setSelectedInk(null);
    } else {
        const errorData = await response.json();
        toast.error(errorData.error || "Failed to permanently delete ink.");
    }
  };

  return (
    <>
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold mb-4">Deleted Ink History</h1>
        <div className="border rounded-md">
          <Table>
            <TableHeader><TableRow>
              <TableHead>Name</TableHead><TableHead>Shade</TableHead><TableHead className="text-right">Actions</TableHead>
            </TableRow></TableHeader>
            <TableBody>
              {deletedInks.map((ink) => (
                <TableRow key={ink.id}>
                  <TableCell>{ink.name}</TableCell>
                  <TableCell>{ink.shade}</TableCell>
                  <TableCell className="text-right space-x-2">
                    <Button variant="outline" size="sm" onClick={() => handleRestore(ink)}>Restore</Button>
                    <Button variant="destructive" size="sm" onClick={() => openDeleteDialog(ink)}>Delete Permanently</Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
         <Link href="/inks" passHref>
            <Button variant="link" className="mt-4">‚Üê Back to Ink Inventory</Button>
        </Link>
      </div>

      <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
        <DialogContent>
          <DialogHeader><DialogTitle>Delete Permanently?</DialogTitle></DialogHeader>
          <DialogDescription>This action cannot be undone and will permanently erase the ink. To proceed, type "Delete" below.</DialogDescription>
          <Input value={confirmText} onChange={(e) => setConfirmText(e.target.value)} className="my-2" placeholder='Type "Delete" to confirm'/>
          <DialogFooter>
            <Button variant="outline" onClick={() => setDialogOpen(false)}>Cancel</Button>
            <Button variant="destructive" onClick={handlePermanentDelete} disabled={confirmText !== "Delete"}>Confirm Permanent Deletion</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
}